* {
    padding: 0;
    margin: 0;
    border: 0;
    }
    *,
    *::before,
    *::after {
    box-sizing: border-box;
    }
    /*Убираем линии обводки некоторых браузеров*/
    :focus,
    :active {
    outline: none;
    }
    a:focus,
    a:active {
    outline: none;
    }
    /*задаем блочными теги Html5*/
    nav,
    footer,
    header,
    aside {
    display: block;
    }
    
    html,
    body {
    font-family: "Lato", sans-serif;
    height: 100%;
    width: 100%;
    
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    }
    /*задаем наследование шрифтов*/
    input,
    button,
    textarea {
    font-family: inherit;
    }
    input::-ms-clear {
    display: none;
    }
    button {
    cursor: pointer;
    }
    button::-moz-focus-inner {
    padding: 0;
    border: 0;
    }
    a,
    a:visited {
    text-decoration: none;
    }
    a:hover {
    text-decoration: none;
    }
    ul li {
    list-style: none;
    }
    img {
    vertical-align: top;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
    font-size: inherit;
    font-weight: 400;
    }
    