@import "_nullStyle.css";

@font-face {
  font-family: Montserrat;
  src: url(../public/fonts/Montserrat-Regular.ttf);
}
@font-face {
  font-family: Fortnite;
  src: url(../public/fonts/fortnitebattlefest.ttf);
}

body {
  background: rgb(24, 24, 24);
  background-position: center;
  background-attachment: fixed;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Fortnite;
  color: #fff;
}



.material-icons {
  font-size: 35px;
}

.basket-list {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 50rem;
  max-width: 95%;
  box-shadow: lpx 3px 8px 5px #bb1600;
  animation: show 500ms ease-in-out;
}
@keyframes show {
  from {
    top: 70%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}
.basket-clear {
  cursor: pointer;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  color: #fff;
}
.basket-quantity {
  z-index: 20;
  cursor: pointer;
  vertical-align: middle;
  color: #000000;
  font-size: 20px;
}




/* ////////////// */

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cart {
  cursor: pointer;
}
.collection {
  z-index: 2;
  position: fixed;
  margin-top: 69px;
  order: 1;
  width: 100%;
  background-color: #be0000;
  padding: 8px 24px;
}
.collection-title {
  font-size: 32px;
}
.collection-item img {
  height: 18px;
  width: auto;
  margin-left: 4px;
}
.collection-item {
  margin: 0 auto;
  width: 30%;
  padding: 8px 0;
  display: flex;
  align-items: center;
  justify-content: start;
 
}
.basket-quantity:nth-child(1) {
  margin-left: 16px;
}
.basket-quantity:nth-child(2) {
  margin-right: 16px;
}
.collection-item span {
  justify-self: flex-end;
  margin-left: auto;
  cursor: pointer;
}
.collection-item i {
  font-size: 24px;
}
.collection-sum {
  margin-top: 16px;
  font-size: 20px;
}
.collection-sum img {
  height: 28px;
  width: auto;
  margin-left: 8px;
}

.toast-container {
  z-index: 20;
  background-color: yellow;
  color: #000;
  width: 100%;
  text-align: center;
  position: fixed;
  top: 69px;
  animation: fade-in 250ms ease-in-out;
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.header {
  z-index: 10;
  position: fixed;
  order: 0;
  width: 100%;
  height: 69px;
  padding: 10px 10vw;
  background-color: #1f00aa;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header h1 {
  text-transform: uppercase;
  font-size: 40px;
  margin-top: -10px;
}
.cart {
  display: flex;
  align-items: center;
}
.cart span {
  font-size: 28px;
  margin: -4px 0 0 8px;
}

/* ------------------------------ */

.goods {
  z-index: 1;
  order: 2;
  width: auto;
  max-width: 1400px;
  min-height: calc(100vh - 70px - 64px);
  padding-top: 96px;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: center;
  gap: 2rem;
  margin-top: 200px;
}
.goods.is_active {
  margin-top: 0;
}
.card {
  width: 420px;
  position: relative;
  cursor: pointer;
  z-index: 5;
  display: flex;
  flex-direction: column;
}
.card-image {
  width: 100%;
  height: auto;
  background-color: #da0700;
}
.card-content {
  background-color: #15171a;
  font-family: Montserrat;
  padding: 3% 6%;
  font-size: 16px;
}
.card-action {
  background-color: #3a3e44;
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 40px;
}
.card-action button {
  justify-self: flex-start;
  margin-right: auto;
  background-color: yellow;
  font-size: 28px;
  line-height: 100%;
  padding: 4px 16px 8px 16px;
}
.card-action p {
  font-size: 28px;
  padding: 4px 0 8px 0;
}
.card-action img {
  display: inline-block;
  height: 70px;
  width: auto;
  margin-left: 0.5rem;
  transform: rotate(25deg);
}
@media(max-width:500px) {
  .card {
    width: 315px;
  }
  .card-content {
    padding: 2.25% 4.5%;
  }
  .card-action {
    height: 30px;
  }
  .card-action button {
    font-size: 21px;
    padding: 3px 12px 6px 12px;
  }
  .card-action p {
    font-size: 21px;
    padding: 3px 0 6px 0;
  }
  .card-action img {
    height: 52.5px;
  }
}

/* ------------------------------ */

footer {
  font-size: Montserrat;
  background-color: #15171a;
  padding: 8px 10vw;
  margin-top: 80px;
}